// Rem base.
$rem-base: 17px;

// Modular scale ration.
$ratio: 1.275;

// Grid settings.
$container-width: 60rem;
$columns: 12;

// Breakpoints.
$breakpoint-sm: '(min-width: 600px)'; // 600
$breakpoint-md: '(min-width: 768px)'; // 768
$breakpoint-lg: '(min-width: 1280px)'; // 1280
$breakpoint-xl: '(min-width: 1440px)'; // 1440

// Colors.
$color-primary: #0bf;
$color-secondary: #f00;
$color-success: #60aa53;
$color-error: #f00;
$color-warning: #e89414;

// Greyscale.
$black: #010101;
$greys: #313233 #595a5c #646566 #87888a #bec0c2 #d7d8da #e8e9eb #f7f8f9;
$white: #fefefe;

// Debug Settings.
$debug-color-primary: rgba(255, 9, 206, 0.5);

// Font weights.
$fw-light: 200;
$fw-book: 300;
$fw-medium: 400;
$fw-semibold: 500;
$fw-bold: 600;

// Defaults.

// Typography.
$default-type-family: 'Abel', 'Arial', serif;
$default-type-color: #313233;
$default-line-height: 1.7;
$defaul-type-weight: 200;

$display-type-family: 'Simplon Norm', sans-serif;
$display-type-transform: uppercase;
$display-letter-spacing: 0.1em;
$display-line-height: 1.5;


$length-increments: 0.6 1.6 2.6 4.2 6.8 11 18 29 46 75 !default;

// Borders.
$default-border-radius: 2px;
$default-border-weight: 1px;
$default-border-color: #e8e9eb;

// Shadow
$default-box-shadow: 0 20px 24px -14px rgba(0, 0, 0, 0.1);
